import { Component, OnInit } from '@angular/core';

import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { ProgramSetupService } from 'src/app/modules/admin/program/program-setup/program-setup.service';


@Component({
  selector: 'app-add-program-setup',
  templateUrl: './add-program-setup.component.html',
  styleUrls: ['./add-program-setup.component.scss']
})
export class AddProgramSetupComponent implements OnInit {

  isView:boolean = false;
  category:string = '';

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private programSetupService: ProgramSetupService
  ) { }

  ngOnInit(): void {

    if(this._activatedRoute.params['value'].type == undefined) {
      this.isView = true
    }
    else {
      this.isView = false;
    }

    this.programSetupService.programstupmatdrawercast.subscribe((res:any) => {

      if(res != null) {
        if(res.id != undefined && res.id == this._activatedRoute.params['value'].id){
          this.isView = false;
          this.category = res.category
          console.log(this.category)
        }
      }

    });

  }

}
